
import { ICON_PATH } from './../../../const/icon-path.const';

import { Component, OnInit } from '@angular/core';
import { ITableSetting } from 'projects/tc-lib/src/lib/interfaces/table-setting';
import * as moment from 'moment';
import { DateType, SwiperDatePickerService, TaiwanDatePipe } from 'merc-lib';
import { FormBuilder, FormGroup } from '@angular/forms';
import { courseList } from './mock';
import { DEVICE } from '../../../../../../../tc-lib/src/lib/const/device.const';
import { SharedService } from 'projects/tc-front/src/app/shared/shared.service';
import { PageSet } from 'projects/tc-front/src/app/core/models/pageset';


@Component({
  selector: 'app-course-record',
  templateUrl: './course-record.component.html',
  styleUrls: ['./course-record.component.scss']
})
export class CourseRecordComponent implements OnInit {
  get ICON_PATH(): any {
    return ICON_PATH;
  }
  DEVICE = DEVICE;

  pageSet = new PageSet();
  validateForm: FormGroup = this.fb.group({
    subject: [''],
    display_startDate: [''],
    startDate: [''],
    display_endDate: [''],
    endDate: [''],
    type: [''],
    class: ['']
  });

  // TODO:假資料
  listOfData: any[] = courseList;

  tableSetting: ITableSetting[] = [
    { header: '課程名稱', primaryKey: 'title' },
    { header: '類別', primaryKey: 'type' },
    { header: '地區', primaryKey: 'location' },
    { header: '期別', primaryKey: 'class' },
    { header: '成績', primaryKey: 'score' },
    { header: '完訓狀態', primaryKey: 'finishing' },
    { header: '完訓日期', primaryKey: 'period' },
  ];

  constructor(
    public sharedService: SharedService,
    private fb: FormBuilder,
    private swiperDatePickerService: SwiperDatePickerService,
    private taiwanDatePipe: TaiwanDatePipe,
  ) { }

  ngOnInit(): void {
    this.pageSet.initialize(courseList.length);
    this.handleShowData();
    this.initSearchDate();
  }

  onClearClick(): void {
    this.validateForm.reset();
  }

  scrollbarControl(): object {
    if (this.sharedService.userDevice === DEVICE.MOBILE) {
      return { x: '990px' };
    } else {
      return {};
    }
  }

  /** 預設搜尋日期 */
  initSearchDate(): void {
    const startDate = moment().subtract(2, 'M').valueOf();
    const endDate = moment().add(1, 'M').valueOf();
    this.validateForm.get('display_startDate')?.patchValue(this.taiwanDatePipe.transform(startDate, true));
    this.validateForm.get('startDate')?.patchValue(startDate);
    this.validateForm.get('display_endDate')?.patchValue(this.taiwanDatePipe.transform(endDate, true));
    this.validateForm.get('endDate')?.patchValue(endDate);
  }
  // 日曆相關 start
  onStartDateClick(): void {
    const form = this.validateForm.getRawValue();
    const defaultDate = form.startDate || moment().subtract(2, 'M').valueOf();
    const maxDate = form.endDate || undefined;
    this.swiperDatePickerService.openDatePicker({ defaultDate, dateType: DateType.startDate }).afterClose.subscribe(date => {
      if (date) {
        this.validateForm.get('display_startDate')?.patchValue(this.taiwanDatePipe.transform(date, true));
        this.validateForm.get('startDate')?.patchValue(date);

        // 如果大於迄日則重置迄日
        const endDate = form.endDate;
        if (endDate && moment(endDate).isBefore(date)) {
          this.validateForm.get('display_endDate')?.patchValue('');
          this.validateForm.get('endDate')?.patchValue('');
        }
      }
    });
  }
  onEndDateClick(): void {
    const form = this.validateForm.getRawValue();
    const defaultDate = form.endDate || form.startDate;
    const minDate = form.startDate || null;
    this.swiperDatePickerService.openDatePicker({ defaultDate, minDate, dateType: DateType.endDate }).afterClose.subscribe(date => {
      if (date) {
        this.validateForm.get('display_endDate')?.patchValue(this.taiwanDatePipe.transform(date, true));
        this.validateForm.get('endDate')?.patchValue(date);
      }
    });
  }
  // 日曆 end

  // pageset start
  /** 換頁 */
  onChangePageIndex(pageIndex: number): void {
    if (pageIndex < 1 || pageIndex > this.pageSet.totalPages) { return; }
    this.pageSet.currentPage = pageIndex;
    this.handleShowData();
    console.log(this.pageSet.currentPage);
  }

  /** 顯示資料 */
  handleShowData(): void {
    this.listOfData = courseList.slice(
      (this.pageSet.currentPage - 1) * this.pageSet.currentPageSize,
      (this.pageSet.currentPage) * this.pageSet.currentPageSize
    );
  }

}
