
import { AddSuccessModalComponent } from './../add-success-modal/add-success-modal.component';
import { ICON_PATH } from './../../../../const/icon-path.const';
import { Component, OnInit } from '@angular/core';
import { FormBuilder, Validators } from '@angular/forms';
import { NzModalRef, NzModalService } from 'ng-zorro-antd/modal';
import { MessageService, validateIdCard } from 'merc-lib';
import { errorMsg } from 'projects/tc-lib/src/public-api';

@Component({
  selector: 'app-add-employee-modal',
  templateUrl: './add-employee-modal.component.html',
  styleUrls: ['./add-employee-modal.component.scss']
})
export class AddEmployeeModalComponent implements OnInit {
  get ICON_PATH(): typeof ICON_PATH {
    return ICON_PATH;
  }
  errorMsg = errorMsg;

  validateForm = this.fb.group({
    id: ['', [Validators.required, validateIdCard()]],
    email: ['', [Validators.required, Validators.email]]
  });

  constructor(
    public modalRef: NzModalRef,
    private fb: FormBuilder,
    private modal: NzModalService,
    private messageSvc: MessageService
  ) { }

  ngOnInit(): void {
  }


  onSendClick(): void {
    const sussess = Math.random() * 10 > 5;
    const controls = this.validateForm.controls;
    for (const i in controls) {
      if (controls.hasOwnProperty(i)) {
        controls[i].markAsTouched();
        controls[i].updateValueAndValidity();
      }
    }
    if (this.validateForm.invalid) { return; }

    if (sussess) {
      this.modalRef.close();
      this.modal.create({
        nzContent: AddSuccessModalComponent,
        nzWidth: 600,
        nzMaskClosable: false,
        nzFooter: null,
        nzBodyStyle: { padding: '12px 24px' },
      });
    } else {
      this.messageSvc.errorModal('此人目前已為業務員，非可增員對象', { imgPath: './assets/image/icon/icon_alert@3x.png', msgTitle: '錯誤訊息' });
    }
  }
}
