


import { AddEmployeeModalComponent } from './add-employee-modal/add-employee-modal.component';
import { NzModalService } from 'ng-zorro-antd/modal';
import { Component, OnInit } from '@angular/core';
import { ITableSetting } from 'projects/tc-lib/src/lib/interfaces/table-setting';
import { mockTable } from './mock';
import { SharedService } from 'projects/tc-front/src/app/shared/shared.service';
import { DEVICE } from '../../../../../../../tc-lib/src/lib/const/device.const';
import { PageSet } from 'projects/tc-front/src/app/core/models/pageset';
import { ICON_PATH } from '../../../const/icon-path.const';

@Component({
  selector: 'app-add-employee',
  templateUrl: './add-employee.component.html',
  styleUrls: ['./add-employee.component.scss']
})
export class AddEmployeeComponent implements OnInit {
  get ICON_PATH(): typeof ICON_PATH {
    return ICON_PATH;
  }
  DEVICE = DEVICE;
  pageSet = new PageSet();
  listOfData: any[] = mockTable;

  tableSetting: ITableSetting[] = [
    { header: '身分證字號', primaryKey: 'id' },
    { header: '電子郵件', primaryKey: 'email' },
    { header: '增員日期', primaryKey: 'startDate' },
    { header: '狀態', primaryKey: 'status' },
    { header: '增員期限', primaryKey: 'endDate' },
  ];
  constructor(
    private nzModal: NzModalService,
    public sharedService: SharedService
  ) { }

  ngOnInit(): void {
    this.pageSet.initialize(mockTable.length);
    this.handleShowData();
  }

  addEmployee(): void {
    this.nzModal.create({
      nzContent: AddEmployeeModalComponent,
      nzWidth: 600,
      nzMaskClosable: false,
      nzFooter: null,
      nzBodyStyle: { padding: '12px 24px' },
    });
  }

  scrollbarControl(): object {
    if (this.sharedService.userDevice === DEVICE.MOBILE) {
      return { x: '570px' };
    } else {
      return {};
    }
  }

  // pageset start
  /** 換頁 */
  onChangePageIndex(pageIndex: number): void {
    if (pageIndex < 1 || pageIndex > this.pageSet.totalPages) { return; }
    this.pageSet.currentPage = pageIndex;
    this.handleShowData();
    console.log(this.pageSet.currentPage);
  }

  /** 顯示資料 */
  handleShowData(): void {
    this.listOfData = mockTable.slice(
      (this.pageSet.currentPage - 1) * this.pageSet.currentPageSize,
      (this.pageSet.currentPage) * this.pageSet.currentPageSize
    );
  }
}
