import { ChangePasswordComponent } from './content/change-password/change-password.component';
import { Component, ElementRef, OnInit, ViewChild } from '@angular/core';
import { Tab } from 'merc-lib';
import { AccountService } from '../../core/services/account.service';
import { AccountType } from '../../core/enums/account-type.enum';


@Component({
  selector: 'app-personal-info',
  templateUrl: './personal-info.component.html',
  styleUrls: ['./personal-info.component.scss'],
})
export class PersonalInfoComponent implements OnInit {
  @ViewChild(ChangePasswordComponent) changePwd: ChangePasswordComponent | undefined;
  // tab selectedIndex
  selectedIndex = 0;
  tabList: Tab[] = [
    {
      id: 0,
      tabName: '個人基本資料'
    },
    {
      id: 1,
      tabName: '訓練記錄'
    },
    {
      id: 2,
      tabName: '增員清單',
    }, {
      id: 3,
      tabName: '變更密碼'
    }
  ];
  AccountType = AccountType;
  constructor(
    public accountService: AccountService
  ) { }

  ngOnInit(): void {
    this.filterTab();
  }

  private filterTab(): void {
    if (this.accountService.account.account_type === AccountType.sales) {
      this.tabList = this.tabList.filter((tab) => tab.id !== 3);
    } else if (this.accountService.account.account_type === AccountType.newRecruit) {
      this.tabList = this.tabList.filter((tab) => tab.id !== 2);
    }
  }

  /** 選取tab序號 */
  selectTab(index: number): void {
    this.selectedIndex = index;
  }

  // changepwd component
  onSendClick(): void {
    this.changePwd?.onSendClick();
  }
  onCancel(): void {
    this.changePwd?.onCancel();
  }
}
