import { AccountType } from './../../../../core/enums/account-type.enum';
import { AccountService } from './../../../../core/services/account.service';
import { Component, Input, OnInit } from '@angular/core';
import { FormGroup, Validators, FormBuilder } from '@angular/forms';
import { validateIdCard, validateMobile, SwiperDatePickerService, TaiwanDatePipe } from 'merc-lib';
import * as moment from 'moment';
import { NzModalService } from 'ng-zorro-antd/modal';
import { eduList, nationalityList, personInfo } from 'projects/tc-front/src/app/shared/mocks/mock';
import { errorMsg } from 'projects/tc-lib/src/public-api';
import { CRUD_CONFIG } from '../../../const/crud.const';
import { ICON_PATH } from '../../../const/icon-path.const';

@Component({
  selector: 'app-basic-info',
  templateUrl: './basic-info.component.html',
  styleUrls: ['./basic-info.component.scss']
})
export class BasicInfoComponent implements OnInit {
  get ICON_PATH(): typeof ICON_PATH {
    return ICON_PATH;
  }
  // form error msg
  errorMsg = errorMsg;
  AccountType = AccountType;

  // 假資料 start
  nationalityList = nationalityList;
  eduList = eduList;
  // 假資料 end


  validateForm: FormGroup = this.fb.group({
    name: ['', Validators.required],
    enLastName: [''],
    enName: [''],
    birth: [''],
    sex: [{ value: '', disabled: true }, Validators.required],
    id: ['', [validateIdCard(), Validators.required]],
    country: ['台灣'],
    education: [''],
    school: ['大學'],
    mobile: ['', [validateMobile(), Validators.required]],
    email: ['', [Validators.email, Validators.required]],
    director: ['10000 林阿嬌 業務主任'],
    referee: ['10000 王小明 業務員']
  });


  constructor(
    private fb: FormBuilder,
    public accountService: AccountService
  ) { }

  ngOnInit(): void {
    this.validateForm.patchValue(personInfo);
    this.validateForm.disable();
  }

  editContact(): void {
    this.validateForm.get('mobile')?.enable();
    this.validateForm.get('email')?.enable();
  }

  exitEdit(): void {
    this.validateForm.patchValue(personInfo);
    this.validateForm.disable();
  }

  saveContact(): void {
    const controls = this.validateForm.controls;
    for (const i in controls) {
      if (controls.hasOwnProperty(i)) {
        controls[i].markAsDirty();
        controls[i].updateValueAndValidity();
      }
    }
    if (this.validateForm.invalid) { return; }
    this.validateForm.disable();
  }
}
